import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Storage } from '@ionic/storage';
import { Platform } from '@ionic/angular';
import {environment} from '../../../environments/environment';
import { BehaviorSubject } from 'rxjs';


@Injectable({
    providedIn: 'root'
})
export class AccountService {

    authState = new BehaviorSubject(false);
    userData = new BehaviorSubject(false);
    
    constructor(public http: HttpClient, public storage: Storage, private platform: Platform) {    
    
        this.platform.ready().then(() => {
            this.ifLoggedIn();
        });    
 
    }
  
  
    ifLoggedIn() {
        this.storage.get('health_token').then((response) => {
            if (response) {
              this.authState.next(true);
            }
        });
    }    
    
    
    public isAuthenticated() {
        return this.authState.value;
    }
    
    
    public login(data){
        return new Promise((resolve, reject) => {
            this.http.post(environment.apiUrl + "/auth/login", data).subscribe((res) => {
                    this.storage.set("health_token", res["token"]).then(() => {
                        this.authState.next(true);
                    });
                             
                    this.retreiveUserData().then(() => {});
                    
                    resolve(res["token"]);             
              },(e) => {
              //console.log(e);
                  reject(e);
              });          
        })
    }
  
  
  
    public register(){
        return new Promise((resolve, reject) => {
            this.http.post(environment.apiUrl + "/auth/signupanonymous", {}).subscribe((res) => {
                this.storage.set("health_token", res["token"]).then(() => {
                    this.authState.next(true);
                }); 
                
                this.retreiveUserData().then(() => {});
                    
                resolve(res["token"]);
            },(e) => {
                reject(e);
            });          
        })      
    }
    
    public retreiveUserData(){
        return new Promise((resolve, reject) => {
            this.storage.get("health_token").then((token) => {            
                if (token){            
                    this.http.get(environment.apiUrl + "/users/me?token=" + token).subscribe((res) => {

                        this.storage.set("health_user", res);
                        this.setUserData(res);
                        resolve(res);
                    }, (e) => {
                    //console.log(e);
                        if (e && e.error && e.error.error && e.error.error.status_code === 401){
                                //token expired, logout
                                this.storage.clear();
                                this.authState.next(false);
                                //this.app.getRootNav().setRoot(LoginPage);
                                //this.app.getRootNav().popToRoot();  
                        }
                        reject(e);
                    }); 
                }
                else{
                    reject();
                }
            });
        });        
    }
    
    
    public getPurchases(){
        return new Promise((resolve, reject) => {
            this.storage.get("health_token").then((token) => {            
                if (token){            
                    this.http.get(environment.apiUrl + "/purchases?token=" + token).subscribe((res) => {

                        resolve(res);
                    }, (e) => {
                        reject(e);
                    }); 
                }
                else{
                    reject();
                }
            });
        });        
    }    
    
    
    public getUserData(){
        return this.userData.asObservable();
    }

    public setUserData(userData){
        this.userData.next(userData);
    }  
    
    
    public resetPassword(data){
        return new Promise((resolve, reject) => {
            this.http.post(environment.apiUrl + "/auth/recovery", data).subscribe((res) => {        
                resolve(res);
            },(e) => {
                reject(e);
            });          
        })      
    }     

    public logout(){
        return new Promise((resolve, reject) => {
            
            this.storage.get("health_token").then((token) => {            
                if (token){
                    this.http.post(environment.apiUrl + "/auth/logout?token=" + token,{}).subscribe((res) => {
                        
                        
                        resolve();
                        
                    }, (e) => {
                        resolve();
                    });   
                }
                else{
                    resolve();
                }
                this.storage.clear();
                this.authState.next(false);
            });       
        })        
    }  
          

    public changePassword(email,password,passwordConfirm){
        return new Promise((resolve, reject) => {
            this.storage.get("health_token").then((token) => {            
                if (token){            
                    this.http.put(environment.apiUrl + "/users/me?token=" + token,{email:email,password:password}).subscribe((res) => {
                        resolve();                     
                    },(e) => {
                        reject(e);
                    });   
                }
                else{reject();} 
            });                    
        })       
    }    
    
    
    public changeEmail(email){
        return new Promise((resolve, reject) => {
            this.storage.get("health_token").then((token) => {            
                if (token){            
                    this.http.put(environment.apiUrl + "/users/me?token=" + token,{email:email}).subscribe((res) => {
                        resolve();                     
                    },(e) => {
                        reject(e);
                    });   
                }
                else{reject();} 
            });                    
        })       
    }      
    
}
