import {fetch as fetchPolyfill} from 'whatwg-fetch';
window.fetch = fetchPolyfill;

import { Component, NgZone} from '@angular/core';

import { Platform } from '@ionic/angular';
import { SplashScreen } from '@ionic-native/splash-screen/ngx';
import { StatusBar } from '@ionic-native/status-bar/ngx';

import { Router, RouterEvent, NavigationEnd } from '@angular/router';
import { AccountService } from './services/account/account.service';

import { Storage } from '@ionic/storage';

declare const cordova: any;

@Component({
  selector: 'app-root',
  templateUrl: 'app.component.html',
  styleUrls: ['app.component.scss']
})
export class AppComponent {
    
    public user:any;
    
    constructor(public platform: Platform, private splashScreen: SplashScreen, private statusBar: StatusBar, 
        private accountService: AccountService, private router: Router, public storage: Storage, 
        private ngZone:NgZone){

        this.initializeApp();
        
        
        this.storage.create().then(() => {       
            this.storage.get('health_token').then((token) => {
                
                if (!token){
                    //generate user account
                    this.accountService.register().then(() => {

                    })
                }
                else{
                    this.accountService.retreiveUserData().then(() => {
                        
                    })
                }
            }).catch(() => {
                
            })        
        })

        
        
        
        
    }


    

    initializeApp() {
        this.platform.ready().then(() => {
            this.statusBar.styleDefault();
            if (this.platform.is("android")) {
                this.statusBar.backgroundColorByHexString("#2d8cb9");
                this.statusBar.styleLightContent();
            }
            else{
                this.statusBar.styleLightContent();
            }
        });
        
        
        setTimeout(() => {this.splashScreen.hide(); },500);

    }
    

}
